import React from 'react';
import Image from 'next/image';
import heroImage from '../../../public/statics/hero.png';

const HeroSection = () => {
    return (
        <section className="relative w-full min-h-[500px] overflow-hidden">
            {/* Фоновое изображение */}
            <div className="absolute inset-0 z-0">
                <Image
                    src={heroImage}
                    alt="Центр бариатрической хирургии"
                    fill
                    className="object-cover"
                    priority
                />
            </div>

            {/* Контент поверх изображения */}
            <div className="relative z-10 w-full px-40 pt-20 pb-16">
                <div className="flex flex-col lg:flex-row items-start justify-between gap-8">
                    {/* Левая часть - вертикальный заголовок */}
                    <div className="lg:w-2/5">
                        <div className="text-white font-bold text-3xl md:text-4xl uppercase tracking-tight leading-tight">
                            <div className="text-4xl md:text-5xl mb-2">Центр</div>
                            <div className="text-3xl md:text-4xl mb-2">бариатрической</div>
                            <div className="text-2xl md:text-3xl mb-2">хирургии в</div>
                            <div className="text-xl md:text-2xl">Волгограде</div>
                        </div>
                    </div>

                    {/* Правая часть - текст и кнопки */}
                    <div className="lg:w-3/5">
                        <div className="mb-8">
                            <h2 className="text-xl md:text-2xl font-bold text-white mb-4">
                                Ваше здоровье и комфорт — наша цель.
                            </h2>
                            <div className="space-y-3">
                                <p className="text-white text-base md:text-lg">
                                    Мы предлагаем эффективные решения, которые меняют вашу жизнь к лучшему!
                                </p>
                                <p className="text-white text-base md:text-lg">
                                    Современные методики, опытные специалисты и индивидуальный подход к каждому пациенту.
                                </p>
                            </div>
                        </div>

                        <div className="flex flex-wrap gap-4">
                            <button className="bg-red-600 hover:bg-red-700 text-white px-6 py-3 rounded-lg font-semibold text-base transition-colors">
                                Записаться
                            </button>
                            <button className="bg-transparent border-2 border-white text-white hover:bg-white/20 px-6 py-3 rounded-lg font-semibold text-base transition-colors">
                                Позвонить
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default HeroSection;